.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background: #e5e5e5;
}

.container {
  width: 665px;
  margin: 0 auto;
  display: flex;
  flex-wrap: nowrap;
}

p {
  margin-block-start: 0;
}

button {
  outline: none;
  border: none;
  cursor: pointer;

  &:active {
    transform: scale(0.95);
  }
}
